<template>
    <div id="search">
        <!-- 标题框 -->
      <van-nav-bar title="搜索"  fixed placeholder/>
      <!-- 搜索框 -->
      <van-search v-model="searchValue" clearable  show-action placeholder="请输入搜索关键词"  @input="onSearch" @search="onSearch">
          <template #action>
              <div @click="onSearch">搜索</div>
         </template>
      </van-search>
      <!-- 商品列表 -->
      <div class="shopperList">
<van-card  v-for="item,i in searchList" :key="i" price="20起送"  :thumb="baseUrl+'/'+item.photo"  @click="toShopper(item.id)">
       
<!-- title -->
 <template #title>
     <div>
          <van-tag size="large" type="warning">品牌</van-tag>
<span class="shopperTitle">{{item.name}}</span>
     <div style="float:right">
          <van-tag plain type="danger">准</van-tag>
          <van-tag plain type="danger" style="margin:5px">保</van-tag>
          <van-tag plain type="danger">票</van-tag>
     </div>
     </div>
     
   
  </template>
  <!-- desc商品描述 -->
  <template #desc>
<van-rate readOnly :count="5" allow-half :value="item.goodsScore" :size="12" /> 
<b> {{item.goodsScore}}</b>
<span style="color:#f2a248"> 月销量{{item.sales}}单</span>
  </template>
  <!-- price配送费 -->
  <template #price>
      ￥{{item.minPrice}}起送 / 配送费约￥{{item.transportationPrice}}
  </template>

</van-card>
      </div>
      <nav-com></nav-com>
    </div>
</template>
<script>
import navCom from '../components/Nav.vue'
export default {
    name:"search",
    components:{
        navCom
    },
    data(){
        return{
             baseUrl:'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT',//基准地址
            searchValue:'',//搜索框值
            searchList:[],//搜索出来的商店列表
        }
    },
    created(){
          this.onSearch();
    },
    methods:{
        //跳转商家详情方法
        toShopper(id){
           this.$router.push({
               name:"shopper",
               params:{id:id}
           })
        },
        onSearch(){
            var searchValue = this.searchValue.replace(/\s*/g,"");
            var url='/biz/queryAllShopsInfo';
            if(searchValue!=null && searchValue!=''){
             url='/biz/queryAllShopsInfoByName?name='+searchValue;
            }
            this.$axios.get(url).then((res)=>{
                // console.log(res.data)
                this.searchList=res.data;
            })
        }
    }
}
</script>
<style scoped>
 .shopperList .shopperTitle{
       font-size: 16px;
       font-weight: bolder;
       margin: 10px;
  }
</style>