<template>
    <div id="me">
         <!-- 标题框 -->
      <van-nav-bar title="我的"  fixed placeholder/>
      <div class="header-box">
     <div style="float:left;margin-right:5vw">
<van-image v-if="isLogin==1"
  round
  width="10vh"
  height="10vh"
  fit="cover"
  :src="require('../assets/head.jpg')"
/>  
<van-image v-else
  round
  width="10vh"
  height="10vh"
  fit="cover"
  src="https://img01.yzcdn.cn/vant/cat.jpeg"
/>  
          </div>

<div class="header-box-title">
    <div class="header-box-title-top" v-if="isLogin==0" ><router-link to="/Login">登录/注册</router-link></div>
    <div class="header-box-title-top van-ellipsis" v-else >{{loginUser.name || loginUser.account}}</div>
    <div class="header-box-title-bottom"><van-icon name="graphic" /> 暂未绑定手机号 </div>
</div>
<div style="float:right;height:10vh;line-height:10vh">
<van-icon v-if="isLogin==1" name="arrow" @click="showLogout=!showLogout"/>
<van-icon v-else name="arrow" />
</div>
      </div>
      <!-- 三公格 -->
      <div class="middle" style="margin-bottom:2vh">
           <van-grid :column-num="3">
  <van-grid-item  > 
      <van-cell  label="我的余额" >
          <template #title><b style="color:#f2a141;font-size:25px">0.00 </b><b> 元</b></template>
      </van-cell>
  </van-grid-item>
  <van-grid-item  > 
      <van-cell  label="我的优惠" >
          <template #title><b style="color:#ed6e50;font-size:25px">0 </b><b> 个</b></template>
      </van-cell>
  </van-grid-item>
  <van-grid-item  > 
      <van-cell  label="我的积分" >
          <template #title><b style="color:#83c041;font-size:25px">0 </b><b> 分</b></template>
      </van-cell>
  </van-grid-item>
  
</van-grid>

      </div>
     
<!-- 列表 -->
<van-cell center is-link >
      <template #icon><van-icon name="notes-o" color="#36d7b7" size="18"/></template>
      <template #title><b style="margin:5px"> 我的订单</b></template>
</van-cell>
<van-cell center is-link >
      <template #icon><van-icon name="points" color="#ed6e50" size="18"/></template>
      <template #title><b style="margin:5px"> 积分商城</b></template>
</van-cell>
<van-cell center is-link >
      <template #icon><van-icon name="medal-o" color="#f2a141" size="18"/></template>
      <template #title><b style="margin:5px"> 小zhang的外卖会员卡</b></template>
</van-cell>
<van-cell center is-link >
      <template #icon><van-icon name="shop" color="#00a67c" size="18"/></template>
      <template #title><b style="margin:5px"> 服务中心</b></template>
</van-cell>

<van-action-sheet
  v-model="showLogout"
  :actions="[{name:'退出登录'}]"
  cancel-text="取消"
  :round="false"
  @select="logout"
/>
        <nav-com></nav-com>
    </div>
</template>
<script>
import navCom from '../components/Nav.vue'
import {mapState} from 'vuex'
export default {
    name:"me",
    components:{
        navCom
    },
    computed:{
        ...mapState(['isLogin','loginUser']),
    },
    created(){
        this.$store.commit('ifIsLogin');
    },
    data(){
        return{
            showLogout:false//展示退出登录面板
        }
    },
    methods:{
        logout(action,index){
            console.log(index)
            console.log(action)
             this.$dialog.confirm({
                  title:'提示',
             message: '您确定要退出登录吗',
           }).then(() => {
                // on confirm
                console.log(1);
                this.$store.commit('ifIsLogout')
                this.$router.push({path:'/'})
              })
              .catch(() => {
                // on cancel
                console.log(0)
              });
        }
    }
}
</script> 
<style scoped>
.header-box{
    width: 100vw;
    height: 15vh;
    background-color: #00a67c;
    box-sizing: border-box;
    padding: 5vw;
    color: white;
}
.header-box-title{
    display: inline-block;
  height: 10vh;
}
.header-box-title a{
   color: white;
}
.header-box-title>div{
    height: 5vh;
    line-height: 5vh;
}
.header-box-title-top{
    font-weight: bold;
}
.header-box-title-bottom{
    font-size: 10px;
}
.middle .van-cell__title{
    text-align: center;
}
</style>