import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'
import Search from '../views/Search.vue'
import Order from '../views/Order.vue'
import Me from '../views/Me.vue'
import Shopper from '../views/Shopper.vue'
import Login from '../views/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Index,
    name: 'index'
  },
  {
    path: '/Search',
    component: Search,
    name: 'search'
  },
  {
    path: "/Order",
    component: Order,
    name: "order"
  },
  {
    path: '/Me',
    component: Me,
    name: "me"
  },
  {
    path: '/Shopper/:id',
    component: Shopper,
    name: "shopper",
    props: true
  },
  {
    path: '/Login',
    component: Login,
    name: "login"
  }
]

const router = new VueRouter({
  routes
})

export default router
