import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLogin:0,
    loginUser:""
  },
  mutations: {
    ifIsLogin(state){
      var user=localStorage.getItem('loginUser');
      if(user!="" && user!=null){
        user=JSON.parse(user);
        state.loginUser=user;
        state.isLogin=1;
      }
    },
    ifIsLogout(state){
        state.isLogin=0;
        state.loginUser="";
        localStorage.removeItem('loginUser');
    }
  },
  actions: {
  },
  modules: {
  }
})
