<template>
    <div id="login">
<van-nav-bar
  left-text="返回"
  left-arrow
  placeholder
  @click-left="onClickLeft"
/>
<div class="title">
     <h1>硅谷外卖</h1>
</div>
<!-- 登陆注册切换 -->
<van-row>
  <van-col offset="2" span="20">
<van-tabs v-model="active" animated swipeable >
  <van-tab  title="登录" title-style="font-size:20px">
      <!-- 登录 -->
<van-form @submit="onLogin" >
  <van-field
    v-model="loginUser.username"
    name="用户名"
    label="用户名"
    placeholder="用户名"
    clearable
    center
    :rules="[{ required: true, message: '请填写用户名' }]"
  />
  <van-field
    v-model="loginUser.password"
    :type="togglePwd"
    name="密码"
    label="密码"
    clearable
    center
    placeholder="密码"
    :rules="[{ required: true, message: '请填写密码' }]"
  >
  <template #extra>
    <van-switch v-model="loginUser.switchChecked" size="20" active-color="#00a67c"/>
  </template>
  </van-field>
  <van-field
    v-model="loginUser.identify"
    name="验证码"
    label="验证码"
    center
    placeholder="验证码"
    :rules="[{ required: true, message: '请填写验证码' }]"
  >
  <template #extra>
    <div @click.stop="refreshCode()">
        <sIdentify :identifyCode="identifyCode" ></sIdentify>
    </div>
  
  </template>
  </van-field>
  <div style="margin: 20px;">
    <van-button round block type="info" native-type="submit" color="#00a67c" >提交</van-button>
  </div>
</van-form>

  </van-tab>
  <van-tab  title="注册" title-style="font-size:20px">
<!-- 注册 -->
<van-form @submit="onRegister" >
  <van-field
    v-model="registerUser.username"
    name="账号"
    label="账号"
    placeholder="账号"
    clearable
    center
    :rules="[{ required: true, message: '请填写账号' }]"
  />
   <van-field
    v-model="registerUser.name"
    name="用户名"
    label="用户名"
    placeholder="用户名"
    clearable
    center
    :rules="[{ required: true, message: '请填写用户名' }]"
  />
  <van-field
    v-model="registerUser.password"
    :type="toggleRegisterPwd"
    name="密码"
    label="密码"
    clearable
    center
    placeholder="密码"
    :rules="[{ required: true, message: '请填写密码' }]"
  >
  <template #extra>
    <van-switch v-model="registerUser.switchChecked" size="20" active-color="#00a67c"/>
  </template>
  </van-field>
  <van-field
    v-model="registerUser.passwordOnce"
    :type=" toggleRegisterPwdOnce"
    name="确认密码"
    label="确认密码"
    clearable
    center
    placeholder="确认密码"
    :rules="[{ required: true, message: '请输入确认密码' }]"
  >
  <template #extra>
    <van-switch v-model="registerUser.switchCheckedOnce" size="20" active-color="#00a67c"/>
  </template>
  </van-field>
  <div style="margin: 20px;">
    <van-button round block type="info" native-type="submit" color="#00a67c" >注册</van-button>
  </div>
</van-form>

  </van-tab>
</van-tabs>
  </van-col>
</van-row>

    </div>
</template>
<script>
import sIdentify from '../components/Identify.vue'
export default {
    name:"login",
    components:{sIdentify},
    data(){
        return{
            active:0, //页面显示状态，0为登陆状态，1为注册状态
            identifyCode:"",//系统接收的验证码
            //登录页数据
            loginUser:{
                username:"", //应户名
                password:"",//密码
                switchChecked:false, //密码显示状态
                identify:""//用户输入的验证码
            },
            // 注册页数据
            registerUser:{
              username:"",//账号
              name:"", //用户名
              password:"",//密码
              passwordOnce:"",//确认密码
              switchChecked:false,//密码栏显示状态
              switchCheckedOnce:false,//确认密码栏显示状态
            }
            
        }
    },
    created(){
          this.refreshCode();
    },
    computed:{
        //根据开关切换密码显示状态
       togglePwd(){
          return this.loginUser.switchChecked ? "" : "password" 
       },
       toggleRegisterPwd(){
          return this.registerUser.switchChecked ? "" : "password" 
       },
       toggleRegisterPwdOnce(){
          return this.registerUser.switchCheckedOnce ? "" : "password" 
       }
    },
    methods:{
      //刷新验证码
      refreshCode(){
        this.$axios.get('/user/sendCaptcha').then(res=>{
             this.identifyCode=res.data+"";
        })
      },
       //点击注册按钮触发函数
        onRegister(values){
           console.log('submit',values);
           this.registerUser.username=values["账号"].trim();
           this.registerUser.name=values["用户名"].trim();
           this.registerUser.password=values["密码"].trim();
           this.registerUser.passwordOnce=values["确认密码"].trim();
           if( this.registerUser.username!=""
            && this.registerUser.name!=""
            && this.registerUser.password!=""
            && this.registerUser.passwordOnce!=""){
               if(this.registerUser.password==this.registerUser.passwordOnce){
                 var data={
                   "account":this.registerUser.username,"name":this.registerUser.name,"password":this.registerUser.password
                 }
                 this.$axios.post('/user/register',data).then(res=>{
                   console.log(res.data);
                   if(res.data=="account is exit"){
                      this.$toast.fail('账号已存在！');
                   }else if(res.data=="register success"){
                       this.$toast.success('注册成功');
                      //  this.$router.go(0);
                      this.active=0;
                   }else{
                      this.$toast.fail('注册失败！');
                   }
                 })
               }else{
                 this.$toast.fail('两次密码不一致！');
               }
            }else{
               this.$toast.fail('每项不能为空！');
            }
        },
        //点击登录按钮触发函数
        onLogin(values) {
      console.log('submit', values);
      this.loginUser.username=values["用户名"].trim();
      this.loginUser.password=values["密码"].trim();
      this.loginUser.identify=values["验证码"].trim();
      if(this.loginUser.username!=""&&this.loginUser.password!=""){
        var data={
           "account":this.loginUser.username,"password":this.loginUser.password
         }
         if(this.loginUser.identify==this.identifyCode){
         this.$axios.post('/user/login',data).then(res=>{
           console.log(res);
           if(res.data!=""){
             res.data=JSON.stringify(res.data);
             localStorage.setItem("loginUser", res.data);
              this.$toast.success('登陆成功');
             this.$router.go(-1);
           }else{
              this.$toast.fail('用户名或密码不正确！');
           }
         },()=>{ this.$toast.fail('登陆失败');})
         }else{
           this.$toast.fail('验证码错误');
         }
      }
    },
    //点击左侧返回
    onClickLeft(){
      this.$router.go(-1);
    }
    }
}
</script>
<style scoped>
.title{
    width: 100vw;
    text-align: center;
    margin: 10vh 0;
    color: #4aa478;
    font-size: 7vw;
}

</style>