<template>
    <div>
        <!-- 标题框 -->
      <van-nav-bar title="订单"  fixed placeholder/>
      <div v-if="isLogin==0">
          <van-empty description="您还未登录">
       <van-button type="primary" color="#00a67c" @click="$router.push({path:'/Login'})">立即登录</van-button>
           </van-empty>
      </div>
      <div v-else>
          <van-tabs>
  <van-tab title="外卖订单">
      <van-empty v-if="orderList.length==0" description="暂无外卖订单" />
      <van-pull-refresh v-else v-model="refreshing" @refresh="onRefresh" van-clearfix>
  <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="getOrderList"
  >
    <van-cell v-for="item,index in orderList" :key="index" >
        <van-swipe-cell>
<van-card thumb="https://img01.yzcdn.cn/vant/ipad.jpeg" @click="getShopList(item.list)">
<template #title><b style="font-size:16px">{{item.shopName}}</b></template>
<template #desc><p>共含{{item.list.length}}种商品</p></template>
<template #num>创建时间：{{item.roderTime}}</template>
</van-card>
 <template #right>
     <van-button square type="primary" text="再来一单" style="height:100%" />
    <van-button square type="danger" text="删除订单" style="height:100%" @click="removeOrder(item.id)"/>
  </template>

        </van-swipe-cell>
    </van-cell>
  </van-list>
</van-pull-refresh>
  <!-- 订单详情面板 -->
  <van-action-sheet v-model="listShow" title="订单详情" cancel-text="取消" >
  <div class="content">
<van-card
v-for="item,i in shopList"
:key="i"
  :num="item.buyCount"
  price="9.00"
  :desc="item.foodInfo"
  :thumb="baseUrl+'/'+item.foodPhoto"
>
<template #title><b style="font-size:14px">{{item.foodName}}</b></template>
</van-card>
  </div>
</van-action-sheet>
  </van-tab>
  <van-tab title="待评价"> <van-empty  description="暂无待评价的订单" /></van-tab>
  <van-tab title="已退款"><van-empty  description="暂无已退款订单" /></van-tab>
  <van-tab title="其他订单"><van-empty  description="暂无其他订单" /></van-tab>
</van-tabs>
      </div>
        <nav-com></nav-com>
    </div>
</template>
<script>
import navCom from '../components/Nav.vue'
import {mapState} from 'vuex'
export default {
    name:"order",
    components:{
        navCom
    },
    data(){
        return{
            baseUrl:'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT',//基准地址
            orderList:[],//订单信息
            loading: false, //是否处于加载状态，加载过程中不触发load事件
            finished: false,//是否已加载完成，加载完成后不再触发load事件
            refreshing: false,//是否下拉刷新
            listShow:false,//展示订单详情
            shopList:[],//订单中的商品列表
        }
    },
    created(){
      this.$store.commit('ifIsLogin');
      this.getOrderList();
    },
    computed:{
      //获取登陆状态
      ...mapState(['isLogin','loginUser']),  
    },
    methods:{
        //删除订单
        removeOrder(id){
               this.$dialog.confirm({
                  title:'提示',
             message: '确认要删除吗？',
           }).then(() => {
                // on confirm
                console.log(1);
               this.$axios.get('/biz/deleteOrderByOid?orderId='+id).then(res=>{
             if(Number(res.data)>0){
               this.$toast.success('删除成功');
               this.onRefresh();
             }else{
                   this.$toast.fail('删除失败');
             }
         })
              })
              .catch(() => {
                // on cancel
                console.log(0)
              });
        
        },
    //获取选中订单的商品列表
    getShopList(list){
this.listShow=!this.listShow;
  this.shopList=list;
    },
          //商品列表下拉刷新方法
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.getOrderList();
    },
        //获取订单列表
        getOrderList(){
            if(this.isLogin==1){
                  if (this.refreshing) {
                         this.shopperList = [];
                               }
                this.$axios.get('/biz/queryOrdersByUserId?userId='+this.loginUser.id).then(res=>{
                    this.orderList=res.data;
                     this.loading = false;
                    this.finished=true;
                      this.refreshing = false;
                })
            }
        }
    }
}
</script>
