<template>
    <div id="index">
       <van-nav-bar title="山东商业职业技术学院"   fixed placeholder  @click-left="onClickLeft" @click-right="onClickRight">
           <template #left>
    <van-icon name="search" size="18" />
  </template>
   <template #right>
       <div class="logButton" v-if="isLogin==0">
   登录|注册
       </div> 
<van-icon v-else name="weapp-nav" size="24"/>
  </template>
       </van-nav-bar>
       <!-- 轮播 -->
       <van-swipe class="my-swipe" indicator-color="#00a67c">
  <van-swipe-item v-for="group,i in 2" :key="i">
      <van-grid clickable :column-num="4" square>
      <van-grid-item v-for="item,j in routeList" :key="j" :icon="baseUrl+'/'+item.photo" :text="item.name" to="/" />
</van-grid>
  </van-swipe-item>

</van-swipe>
<!-- 商品列表 -->
<div class="shopperList">

<van-pull-refresh v-model="refreshing" @refresh="onRefresh" van-clearfix>
  <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="getShoppers"
  >
    <van-cell v-for="item in shopperList" :key="item.id" >
<van-card  price="20起送"  :thumb="baseUrl+'/'+item.photo" @click="toShopper(item.id)">
       
<!-- title -->
 <template #title>
     <div>
          <van-tag size="large" type="warning">品牌</van-tag>
<span class="shopperTitle">{{item.name}}</span>
     <div style="float:right">
          <van-tag plain type="danger">准</van-tag>
          <van-tag plain type="danger" style="margin:5px">保</van-tag>
          <van-tag plain type="danger">票</van-tag>
     </div>
     </div>
     
   
  </template>
  <!-- desc商品描述 -->
  <template #desc>
<van-rate readOnly :count="5" allow-half :value="item.goodsScore" :size="12" /> 
<b> {{item.goodsScore}}</b>
<div style="color:#f2a248"> 月销量{{item.sales}}单</div>
  </template>
  <!-- price配送费 -->
  <template #price>
      ￥{{item.minPrice}}起送 / 配送费约￥{{item.transportationPrice}}
  </template>

</van-card>
 
    </van-cell>
  </van-list>
</van-pull-refresh>
</div>
<!-- 分享面板 -->
<van-share-sheet
  v-model="showShare"
  title="立即分享给好友"
  :options="options"
/>

<nav-com></nav-com>
    </div>

</template>
<script>
import navCom from '../components/Nav.vue'
import {mapState} from 'vuex'
export default {
    name:"index",
    components:{
        navCom
    },
    computed:{
     ...mapState(['isLogin'])
    },
    data(){
      return{
          baseUrl:'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT',//基准地址
          routeList:[],//九宫格数据列表
          shopperList:[],//商店列表
          loading: false, //是否处于加载状态，加载过程中不触发load事件
          finished: false,//是否已加载完成，加载完成后不再触发load事件
          refreshing: false,//是否下拉刷新
          showShare: false,//是否展示分享面板
          options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' },
        ],
      ],
      }
    },
    created(){
        //九宫格信息
        this.$axios.post('/biz/queryBigCategory').then((res)=>{
            console.log(res.data);
            this.routeList=res.data;
        })
    },
    methods:{
        //跳转搜索页
        onClickLeft(){
            this.$router.push('/Search')
        },
        //跳转到登录、注册页
        onClickRight(){
          if(this.isLogin==0){
            this.$router.push('/Login')
          }else{
            this.showShare=!this.showShare
          }
            
        },
        //跳转商家详情方法
        toShopper(id){
           this.$router.push({
               name:"shopper",
               params:{id:id}
           })
        },
    //商品列表下拉刷新方法
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.getShoppers();
    },

     //获取商店列表方法
        getShoppers(){
             if (this.refreshing) {
          this.shopperList = [];
    
        }
       
            this.$axios.get('/biz/queryAllShopsInfo').then((res)=>{
                   this.shopperList=res.data;
                    this.loading = false;
                    this.finished=true;
                     this.refreshing = false;
            })
        }

  },
       
    
}
</script>

<style scoped>
.logButton{
    color: white;
}
/* 轮播 */
 .my-swipe .van-swipe-item {
    color: red;
    font-size: 20px;
    /* line-height: 150px; */
    text-align: center;
    background-color: white;
  }
  /* 商店列表 */
  .shopperList{
      margin-top: 20px;
  }
  .shopperList .shopperTitle{
       font-size: 16px;
       font-weight: bolder;
       margin: 10px;
  }
</style>