<template>
    <div id="shopper">
   <!-- 标题框 -->
      <van-nav-bar
  left-text="返回"
  left-arrow
  @click-left="onClickLeft"
  z-index="0"
/>
<!-- 商家头像 -->
<div style="width:20vw;margin:0 auto;margin-top:-4vh">
<van-image
  width="20vw"
  height="20vw"
  fit="cover"
  :src="baseUrl+'/'+shopperDetails.photo"
/>
</div>
<!-- 商家名称 -->
<van-row type="flex" justify="center">
    <van-col span="12" >
        <van-cell  center >
  <!-- 使用 title 插槽来自定义标题 -->
  <template #title>
      <div style="width:100%;text-again:center" class="van-ellipsis">
           <van-tag type="warning"  size="medium">品牌</van-tag>
    <span class="custom-title"> {{shopperDetails.name}}</span>
   
      </div>
  </template>
   <template #right-icon>
<van-icon name="play" size="25"/>
  </template> 
 </van-cell>
    </van-col>
</van-row>

<van-row type="flex" justify="center">
  <van-col span="18">{{shopperDetails.goodsScore}} 月售{{shopperDetails.sales}}单  联想教育  约{{shopperDetails.transportationPrice}}分钟 距离{{shopperDetails.distance}}米</van-col>
</van-row>
<!--  -->
<van-row type="flex" justify="center">
  <van-col span="14">
       <van-cell center is-link arrow-direction="down" @click="show=true">
  <!-- 使用 title 插槽来自定义标题 -->
  <template #title>
       <van-tag color="#ffe922" v-if="shopperActive.length>0">{{shopperActive[0].tag}}</van-tag>
    <span class="active-title" v-if="shopperActive.length>0"> {{shopperActive[0].contents}}</span>
    <span  class="active-title" style="font-size:12px;color:darkgray;display:block;float:right;">{{shopperActive.length}}个优惠</span>
  </template>

</van-cell>
  </van-col>
</van-row>
<!-- 优惠列表弹出框 -->
<van-action-sheet v-model="show" title="优惠活动">
  <div class="content">
     <van-cell center  v-for="item,i in shopperActive" :key="i">
  <template #title>
       <van-tag color="#ffe922" >{{item['tag']}}</van-tag>
    <span class="active-title"> {{item.contents}}</span>
  </template>

</van-cell>
  </div>
</van-action-sheet>
<!-- 中间功能区(点菜、评价、商家) -->
<div class="middle">
<van-tabs title-active-color="#4aa478" sticky>
  <van-tab title=" 点餐 ">
      <!-- 点餐界面 -->
     <van-tree-select height="62vh" :items="foodCategory" :main-active-index.sync="activeKey">
  <template #content>
      <div class="foodInfo">
        <van-card  v-for="item,i in foodInfo" :key="i"
 
  price="9.00"
  :desc="item.info"
  :thumb="baseUrl+'/'+item.photo"
>
<template #title><b>{{item.name}}</b></template>
  <template #tags>
   <a style="font-size:8px;">月销售86份 好评率100%</a>
  </template>
  <template #num>
    <van-icon v-if="!toggleInput(item.id)" name="add" color="#4aa478" size="20" @click="pushShoppingCar(item.id)"/>
    <van-stepper v-else theme="round" :min="0" button-size="20" disable-input @plus=" pushShoppingCar(item.id)" @minus="minusShoppingCar(item.id)"/>
  </template>
</van-card>  
      </div>
   
  </template>
</van-tree-select>
<!-- 底部结算栏 -->
<div style="width:100vw;height:6vh"></div>
<van-submit-bar :price="priceAll*100" button-text="提交订单">
        <van-icon name="cart" :badge="countAll" color="#4aa478" size="40" />
        <template #button>
<van-button type="primary" round v-if="priceAll>=shopperDetails.minPrice"  @click="submitOrder">提交订单</van-button>
<van-button type="primary" disabled round v-else>满{{shopperDetails.minPrice}}起送</van-button>
        </template>
</van-submit-bar>
  </van-tab>
  <van-tab title=" 评价 ">
      <!-- 评价内容 -->
      <van-row type="flex" justify="center">
  <van-col span="24">
      <div class="score-title">
          <div class="score-title-left">
             <ul>
                 <li style="font-size:25px;font-weight:bolder;color:orange">{{shopperDetails.score}}</li>
                 <li><b>综合评分</b></li>
                 <li style="font-size:5px;color:darkgray">高于周边商家的99%</li>
             </ul>
          </div>
          <div class="score-title-right">
              <ul>
                  <li>
                      <van-rate :value="shopperDetails.serviceScore" allow-half void-icon="star" void-color="#eee" size="16"/>
                      <span style="font-size:10px;margin:3vw">服务评分 {{shopperDetails.serviceScore}}</span>
                  </li>
                  <li>
                       <van-rate :value="shopperDetails.goodsScore" allow-half void-icon="star" void-color="#eee" size="16"/>
                      <span style="font-size:10px;margin:3vw">商品评分 {{shopperDetails.goodsScore}}</span>
                  </li>
                  <li>
                      <b style="margin-right:3vw;font-size:14px">送达时间</b><a style="color:darkgray;font-size:12px">{{shopperDetails.deliveryTime}}分钟</a>
                  </li>
              </ul>
          </div>
          <div style="clear:both"></div>
      </div>
  </van-col>
</van-row>
<!-- 评论区 -->
<div style="margin-left:3vw;margin-bottom:5vw">
    <van-radio-group v-model="radio" direction="horizontal" checked-color="#4aa478">
  <van-radio name="2" icon-size="16px">全部</van-radio>
  <van-radio name="1" icon-size="16px">只看好评</van-radio>
  <van-radio name="0" icon-size="16px">只看差评</van-radio>
</van-radio-group>
</div>
<div class="speak">
    <van-card v-for="item,i in fitterSpeaking" :key="i" >
<template #thumb>
    <van-image round width="10vw" height="10vw" src="https://img01.yzcdn.cn/vant/cat.jpeg"/>
</template>
<template #title>
    <div style="font-size:12px;margin-top:2vw">{{item.account}}</div>
</template>
<template #desc>
<ul style="margin-top:2vw">
    <li>
         <van-rate :value="item.score/10" allow-half void-icon="star" void-color="#eee" size="16"/>
           <van-tag plain type="warning">{{item.score/10}}分</van-tag>
           <van-tag v-if="item.isGood==1" type="success">好评</van-tag>
           <van-tag v-else type="danger">差评</van-tag>
    </li>
    <li>
      <p>{{item.comments}}</p>
    </li>
</ul>
   
</template>
<template #footer>
<div style="color:darkgray">
    {{item.oederTime}}
</div>
</template>

</van-card>
       
</div>

  </van-tab>
  <van-tab title=" 商家 ">
<!--   商家信息-->
<van-card>
<template #title>
    <h2>配送信息</h2>
</template>
<template #tags>
     <van-tag type="danger">联想外卖</van-tag>
     <span style="line-height:25px;margin-left:2vw">由商家配送提供配送，约{{shopperDetails.deliveryTime}}分钟送达，距离{{shopperDetails.distance}}米，配送费￥{{shopperDetails.transportationPrice}}</span>
</template>
</van-card>
<van-card>
<template #title>
    <h2>活动与服务</h2>
</template>
<template #tags>
<div v-for="item,i in shopperActive" :key="i">
     <van-tag type="primary">{{item.tag}}</van-tag>
     <span style="line-height:25px;margin-left:2vw">{{item.contents}}</span>
</div>
    
</template>
</van-card>
<van-card>
<template #title>
    <h2>商家实景</h2>
</template>
<template #tags>
   <van-row gutter="20">
  <van-col span="8" v-for="item,i in 3" :key="i">
      <van-image src="https://img01.yzcdn.cn/vant/cat.jpeg"/>
  </van-col>
</van-row>
</template>
</van-card>

  </van-tab>
</van-tabs>
</div>

    </div>
</template>
<script>
import {mapState} from 'vuex'
export default {
    name:"shopper",
    props:['id'],
    data(){
        return{
             baseUrl:'http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT',//基准地址
            shopperDetails:[],//商店详情
            shopperActive:[],//商店活动
            show:false,//优惠弹出框显示状态
            activeKey:0,//选中的食品类别
            foodCategory:[],//食品分类信息
            foodInfo:[],//根据视频类别查询出的食品
            shoppingCar:[],//用户添加至购物车的食物
            speaking:[],//商家评论
            radio:"2",//评论过滤模式
        }
    },
    computed:{
        //获取登陆状态
        ...mapState(['isLogin','loginUser']),
        //切换加购图标
      toggleInput(){
          return function(id){
              for(var i in this.shoppingCar){
              if(id==this.shoppingCar[i].foodId){
                  return true;
              }
          }
          return false;
          }  
      },
      //计算总数量
      countAll(){
          var count=0;
          for(var i in this.shoppingCar){
             count+= this.shoppingCar[i].buyCount;
          }
          return count;
      },
      //计算总价格
      priceAll(){
              var count=0;
          for(var i in this.shoppingCar){
             count+= this.shoppingCar[i].buyCount;
          }
          return count*9;
      },
      //过滤评论
      fitterSpeaking(){
          var goodSpeaking=[];
          var badSpeaking=[];
         for(var i in this.speaking){
            if(this.speaking[i].isGood==0){
                badSpeaking.push(this.speaking[i]);
            }else{
                goodSpeaking.push(this.speaking[i]);
            }
         }
         if(this.radio==0){
             return badSpeaking;
         }else if(this.radio==1){
             return goodSpeaking;
         }else{
             return this.speaking;
         }
      }
    },
    watch:{
      activeKey(){
          this.getFoodInfo();
      }
    },
    created(){
       this.getShopperDetails();
       this.getShopperActive();
       this.getFoodCategory();
       this.getFoodInfo();
       this.getSpeaking();
       this.$store.commit('ifIsLogin');
    },
    methods:{
        //提交订单
        submitOrder(){
          if(this.isLogin==0){
              this.$dialog.confirm({
                  title:'提示',
             message: '您还未登录，请先登录！',
           }).then(() => {
                // on confirm
                console.log(1);
                this.$router.push({path:'/Login'})
              })
              .catch(() => {
                // on cancel
                console.log(0)
              });
          }else{
              this.$dialog.confirm({
                  title:"提示",
                  message:"您确定要提交吗"
              }).then(()=>{
                   var data={
                  userId:Number(this.loginUser.id),
                  shopId:Number(this.id),
                  list:this.shoppingCar
              }
              console.log(data)
              this.$axios.post('/biz/insertOrder',data).then(res=>{
                  console.log(res.data)
                  if(res.data==1){
                       this.$toast.success('提交成功！');
                       this.$router.push({path:'/Order'})
                  }else{
                         this.$toast.fail('提交失败！');
                  }
              });
              }).catch(()=>{
                  this.$toast('你再想想吧！');
              })
             
          }
        },
        //获取商家评论内容
        getSpeaking(){
          this.$axios.get('/biz/queryCommentByShopId?shopId='+this.id).then((res)=>{
                this.speaking=res.data;
            })
        },
        //购物车内容计算
        pushShoppingCar(id){
           for(var i in this.shoppingCar){
              if(id==this.shoppingCar[i].foodId  ){
                  this.$set(this.shoppingCar[i],"buyCount",this.shoppingCar[i].buyCount+=1);
                  return;
              }
          }
          this.shoppingCar.push({foodId:id,buyCount:1,shopId:Number(this.id)});
        },
        minusShoppingCar(id){
            for(var i in this.shoppingCar){
              if(id==this.shoppingCar[i].foodId){
                  if(this.shoppingCar[i].buyCount==1){
                      this.shoppingCar.splice(i,1)
                  }else{
                      this.$set(this.shoppingCar[i],"buyCount",this.shoppingCar[i].buyCount-=1);
                  }
                  
                 
              }
          }
        },
         //点击左侧返回
    onClickLeft(){
      this.$router.go(-1);
    },
        //获取商店详情方法
        getShopperDetails(){
            this.$axios.get('/biz/queryInfoByShopId?shopId='+this.id).then((res)=>{
                this.shopperDetails=res.data;
            })
        },
        //查询商家优惠活动
        getShopperActive(){
             this.$axios.get('/biz/querySpecialOfferByShopId?shopId='+this.id).then((res)=>{
                this.shopperActive=res.data;
            })
        },
        //查询商家的食品分类信息
        getFoodCategory(){
             this.$axios.get('/biz/queryFoodCategory?id='+this.id).then((res)=>{
                this.foodCategory=res.data;
                for(var i in this.foodCategory){
                    this.foodCategory[i].text=this.foodCategory[i].name;
                }
            })
        },
        //通过商家的食品分类查询对应的食品
        getFoodInfo(){
             this.$axios.get('biz/queryFoodinfoByShopIdAndFoodCategoryId?shopId='+this.id+'&foodcategoryId='+this.activeKey).then((res)=>{
                this.foodInfo=res.data;
            })
        }
    }


}
</script>
<style scoped>
.custom-title{
    font-size: 20px;
    margin-left: 10px;
    font-weight: bolder;
    vertical-align: middle;
}
.van-col.van-col--18{
    font-size: 10px;
    color: darkgray;
    text-align: center;
}
.active-title{
    font-size: 12px;
    overflow: hidden;
text-overflow:ellipsis;
white-space: nowrap;
}
 .content {
    padding: 16px 16px 30px;
  }
  .foodInfo .van-card__thumb{
      width: 15vw;
  }
  .score-title{
      width: 100%;
      border: 1px solid #eee;
      padding: 2vw 1vw;
      box-sizing: border-box;
      margin: 3vh 0;
      line-height: 30px;
  }
  .score-title-left{
      width: 40%;
      border-right: 1px solid #eee;
      text-align: center;
      padding-right: 2vw;
      /* display: inline-block; */
      float: left;
  }
  .score-title-right{
      /* display: inline-block; */
      float: left;
       width: 52%;
       /* background-color: aqua; */
       margin-left:3% ;  
  }
  .speak .van-card__thumb{
      width: 12vw;
  }
 .van-tree-select__nav{
     flex:0.7
 } 
 
</style>